/* RESET */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* GLOBAL */
html {
    font-family: 'Open Sans', sans-serif; 
    font-size: 14px;   
}

html * {
    box-sizing: border-box;
}

/* SPECIFIC */
.hero-img {
    width: 100vw;
}

.intro-section,
.opening-times-section {
    width: 100vw;
    padding: 4rem 2rem;        
    background-repeat: repeat;
    background-size: 100px;
}

.intro-section {
    background-image: url("repeating-pattern-dark.png");
}

.intro-section p {
    text-align: center;
    max-width: 800px;
    margin: 0 auto;
    font-size: 1.5rem;    
    line-height: 2.5rem;
    letter-spacing: 1px;
    color:white;
}

.opening-times-section {
    min-height: 300px;
    background-color: white;
}

.opening-times-title {
    font-family: 'BigNoodleTitling';
    color: #1C1D1E;
    font-size: 6rem;
    text-shadow: 2px 2px 0px #fff, 4px 4px 0px #1C1D1E;
    text-align: center;
    letter-spacing: .25rem;
    margin-bottom: 1.5rem
}

.opening-time {
    display: table;        
    padding-bottom: 1rem;
    width: 100%;
}

.opening-time:last-of-type {
    padding-bottom: 0;
}

.opening-time__day,
.opening-time__times {
    display: table-cell;
    width: 50%;
    font-size: 1rem;
    letter-spacing: 1px;
}

.opening-time__times {
    text-align: right;
    text-transform: lowercase;
}
.opening-times-list {
    max-width: 400px;
    margin: 0 auto;
    padding: 1rem;
    border: 2px solid #1C1D1E;
    box-shadow: 2px 2px 0px #fff, 4px 4px 0px #1C1D1E;
}

@media (min-width: 800px) {
    html {
        font-size: 16px;
    }
}